import React from 'react'

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Nav, Navbar} from "react-bootstrap";

import { library } from '@fortawesome/fontawesome-svg-core'
import { faFolderOpen as fasFaFolderOpen , faAddressCard as fasFaAddressCard, faGraduationCap as fasFaGraduationCap, faUserTie as fasFaUserTie} from '@fortawesome/free-solid-svg-icons'
import { faFolderOpen as farFaFolderOpen , faAddressCard as farFaAddressCard} from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import '../config/Transitions.css'
import './css/Wrapper.css'
import {MainHeading} from "../config/Typography";

const PAGE_TO_ICON = Object.freeze({
   'ABOUT' : <FontAwesomeIcon icon={['fas', 'address-card']} />,
    'PROFESSIONAL' : <FontAwesomeIcon icon={['fas', 'user-tie']} />,
    'PORTFOLIO' : <FontAwesomeIcon icon={['fas', 'folder-open']} />,
    'EDUCATION' : <FontAwesomeIcon icon={['fas', 'graduation-cap']} />,
});

library.add(farFaFolderOpen, fasFaFolderOpen, farFaAddressCard, fasFaGraduationCap, fasFaUserTie, fasFaAddressCard)

export default class Wrapper extends React.Component {
    clickHandler() {
        const element = document.getElementById('slide-panel');
        element.style.transition = 'none';
        setTimeout(function () {
            element.style.transition = '';
        }, 5);
        element.classList.add('slide-out');
    }
    constructor(props) {
        super(props);
        this.clickHandler = this.clickHandler.bind(this)
    }

    render() {

        return (
            <div className={'wrapper-style'} style={{backgroundColor: this.props.backgroundColor}}>
                <Container>
                    <Row>
                        <Col md={12}>
                            <div className="page-header">
                                <MainHeading> {PAGE_TO_ICON[this.props.currentPage]} About Ash </MainHeading>
                            </div>
                        </Col>
                    </Row>
                    <Row id="slide-panel">
                        <Col>{this.props.children}</Col>
                        <Col md="auto">
                            <div className="nav-panel">
                            <Navbar sticky="top" >

                                <Nav activeKey={window.location.pathname} affix className="flex-column">

                                    <Nav.Link exact activeClassName="navbar__link--active"
                                              className="navbar__link" href={process.env.PUBLIC_URL + '/'} >
                                        <h3>About</h3>
                                    </Nav.Link>
                                    <Nav.Link activeClassName="navbar__link--active"
                                              className="navbar__link" href={process.env.PUBLIC_URL + '/professional'} >
                                        <h3>Professional</h3>
                                    </Nav.Link>
                                    <Nav.Link activeClassName="navbar__link--active"
                                              className="navbar__link" href={process.env.PUBLIC_URL + '/education'}>
                                        <h3>Education</h3>
                                    </Nav.Link>
                                    <Nav.Link activeClassName="navbar__link--active"
                                              className="navbar__link" href={process.env.PUBLIC_URL + '/portfolio'}>
                                        <h3>Portfolio</h3>
                                    </Nav.Link>
                                </Nav>

                            </Navbar>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}